* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  background-color: bisque;
  display: flex;
  align-items: center;
  justify-content: center;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.main-container{
  width: 600px;
  height: 600px;
  display: flex;
  flex-direction: column;
  border: 1px solid black;
  border-radius: 50px;
  padding: 50px;
  background-color: bisque;

}

.finder{
  display: flex;
  flex-direction: column;
  border: 1px solid black;
  align-items: center;
  justify-content: center;
  padding: 50px;
  width: 400px;
  background-color: rgb(120, 120, 158);
}

label{
  font-size: 24px;
  font-weight: bold;
}

.country-list{
  list-style: none;
  margin: 15px;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: start;

}

.country-list >li {
  list-style: none;
  margin: 0px;
  display: flex;
  align-items: center;
  justify-content: start;
  font-weight: bold;
  cursor: pointer;

}

.country-list >li >img {
  list-style: none;
  margin: 10px;

}